.skill {
    height: 100vh;
    width: 100vw;
    padding: 0 0 50px 0;
    position: relative;
  }
  .skill-bx {
    background: #25271e2e;
    border-radius: 64px;
    text-align: center;
    padding: 60px 50px;
    margin-top: 10px;
  }
  .skill h2 {
      font-size: 45px;
      font-weight: 700;
  }
  .skill p {
      color: #B8B8B8;
      font-size: 18px;
      letter-spacing: 0.8px;
      line-height: 1.5em;
      margin: 14px 0 75px 0;
  }
  .skill-slider {
    width: 80%;
    margin: 0 auto;
    position: relative;
  }
  .skill-slider .item img {
    width: 50%;
    margin: 0 auto 15px auto;
  }
  .background-image-left {
    top: 5%;
    position: absolute;
    bottom: 0;
    width: 40%;
    bottom: 0;
    /* border: 2px solid white; */
    align-items: center;
    justify-content: center;
    height: 80%;
    z-index: -4;

  }